<template> 
	<div>
		<img style="width: 100%;" src="https://ccdn.goodq.top/caches/d77fb68e7c449a81135cc696b98e87de/aHR0cDovLzU2OGM4MjkxOWRmYmYudDc0LnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxNi8xMS81NDY5NWFjYzU4MmMzZjdmY2MxMDk0YWU0YTJkOTJlNS5qcGc_p_p100_p_3D.jpg" alt="" />
		<div class="pay_center">
			<div style="text-align: center;" class="pay_title">
				<p style="font-size: 24px;color:#666666;"></p>
				<p style="color:#666666;"><strong>Contact Us</strong></p>
				<i class="glyphicon glyphicon-menu-down" style="font-size: 30px; color: #757575;"></i>
			</div>
			<div class="pay_text2">
				<div style="width: 60%;">
					<div id="allmap" style="width: 100%;height: 300px;"></div>
				</div>
				<div style="width: 40%;">
					<p>99</p>
					<p>99</p>
					<p>0500-6000 0000</p>
					<p>0500-6000 0000</p>
					<div class="xinxi">
						<input type="text" placeholder="()">
						<input type="text" placeholder="()">
						<input type="text" placeholder="">
						<textarea name="" rows="" cols="" placeholder="()">
							
						</textarea>
						<button class="sub"></button>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		mounted(){
			// API
			var map = new BMap.Map("allmap"); // Map
			map.centerAndZoom(new BMap.Point(116.404, 39.915), 11); // ,
			//
			map.addControl(new BMap.MapTypeControl({
				mapTypes: [
					BMAP_NORMAL_MAP,
					BMAP_HYBRID_MAP
				]
			}));
			map.setCurrentCity(""); //  
			map.enableScrollWheelZoom(true); //
		}
	}
</script>

<style>
	.pay_center {
		width: 76%;
		margin-left: 12%;
		margin-bottom: 50px;
	}
	
	.pay_title {
		margin: 20px 0;
	}
	
	.pay_text2 {
		display: flex;
		justify-content: space-between;
	}
	
	.pay_text2 div p {
		font-size: 14px;
		color: #969696;
		margin: 5px;
		font-family: "", Helvetica, Arial, Verdana, sans-serif;
	}
	
	.xinxi {
		width: 70%;
		margin-top: 20px;
		margin-left: 2%;
		font-size: 14px;
	}
	
	.xinxi input {
		width: 318px;
		background-color: rgba(249, 249, 249, 1);
		border: solid 1px #CCC;
		padding: 0 10px;
		color: #666;
		height: 25px;
		line-height: 25px;
		margin-bottom: 8px;
	}
	
	textarea {
		width: 318px;
		height: 63px;
		background-color: rgba(249, 249, 249, 1);
		border: solid 1px #CCC;
	}
	
	.sub {
		width: 70px;
		height: 28px;
		font-size: 14px;
		line-height: 28px;
		text-align: center;
		background-color: rgba(249, 249, 249, 1);
		border: solid 1px #CCC;
	}
</style>


